import { NgModule } from '@angular/core';
import { RouterModule } from '@angular/router';
import { CommonModule } from '@angular/common';
import {
  MdIconModule, MdCardModule, MdButtonModule, MdListModule, MdProgressBarModule, MdMenuModule,
  MaterialModule, MdToolbarModule, MdTabsModule
} from '@angular/material';
import { FlexLayoutModule } from '@angular/flex-layout';

import { ChartsModule } from 'ng2-charts/ng2-charts';
import { NgxDatatableModule } from '@swimlane/ngx-datatable';

import { FacesComponent } from './faces.component';
import { FacesRoutes } from './faces.routing';
import { FaceInstanceComponent } from './face-instance/face-instance.component';
import {DialogService} from "../shared/dialog-service/dialog.service";
import {FormsModule} from "@angular/forms";
import {AgmCoreModule} from "angular2-google-maps/core";

@NgModule({
  imports: [
    MaterialModule,
    CommonModule,
    RouterModule.forChild(FacesRoutes),
    MdIconModule,
    MdCardModule,
    MdButtonModule,
    MdListModule,
    MdProgressBarModule,
    MdMenuModule,
    ChartsModule,
    NgxDatatableModule,
    FlexLayoutModule,
    CommonModule,
    MdCardModule,
    MdToolbarModule,
    MdTabsModule,
    FormsModule,
    FlexLayoutModule,
    AgmCoreModule
  ],
  declarations: [
    FacesComponent
  ],
  exports: [
  ],
  entryComponents: [
  ],
  providers: [
    DialogService
  ]
})

export class FacesModule {}



