import { Component, OnInit, NgZone } from '@angular/core';
import {Face} from './../models/face';
import {PouchdbService} from '../shared/pouchdb-service/pouchdb.service';
import {BaseApiService} from '../shared/api-service/base-api.service';
import {DialogService} from '../shared/dialog-service/dialog.service';

@Component({
  selector: 'app-faces',
  providers: [PouchdbService, BaseApiService],
  templateUrl: './faces.component.html',
  styleUrls: ['./faces.component.scss']
})
export class FacesComponent implements OnInit {

  public faces = new Array<Face>();
  constructor(private dialogsService: DialogService,
              private database: PouchdbService,
              private zone: NgZone,
              private apiService: BaseApiService ) { }

  ngOnInit() {
      this.apiService.get('_design/faces/_view/all').subscribe(result => {
          console.log(result.rows);
          this.faces = result.rows;
      }, error => {
          console.error('error');
      });
  }

    public deleteFace(row: any , $event) {
        this.dialogsService
            .confirm('Delete face ' , 'Are you sure you want to delete ' + row.value.name + '?')
            .subscribe(res => {
                if (res) {
                    this.apiService.del( row.id + '?rev=' + row.value._sync.rev).subscribe(result => {
                            console.log(result);
                            this.faces.splice(row.$$index , 1);
                        }, error => {
                            console.error('error');
                        }
                    );
                }
            });
    }

}



